/* Base Reset */
* { box-sizing: border-box; }
body, html, #root { margin:0; padding:0; height:100%; font-family: system-ui,-apple-system,"Segoe UI",Roboto,Arial,sans-serif; background:#000; color:#fff; }

/* Background logo */
body::before {
  content:"";
  position:fixed;
  inset:0;
  background:
    radial-gradient(circle at center, rgba(0,0,0,.3), #000 70%),
    url("https://blapoker.com/wp-content/uploads/logo_rev.png") center 120px / 600px auto no-repeat;
  opacity:.28;
  pointer-events:none;
  z-index:-1;
}

.app-shell {
  max-width: 900px;
  margin: 0 auto;
  padding: 40px 22px 80px;
}

/* Panels */
.panel {
  background: transparent;
  margin: 0 auto;
}

/* Auth panel */
.auth {
  max-width: 420px;
}

.title {
  font-size: clamp(2.2rem, 5.5vw, 3.2rem);
  margin: 0 0 18px;
  font-weight: 800;
  letter-spacing: .5px;
  text-align:left;
}

.text-input {
  width:100%;
  padding:12px 14px;
  border:1px solid #444;
  border-radius:8px;
  background:#111;
  color:#fff;
  font-size:15px;
  margin-bottom:12px;
}
.text-input:focus {
  outline:2px solid #d72626;
  border-color:#d72626;
}

.swap {
  margin-top:8px;
  font-size:14px;
  cursor:pointer;
  color:#eee;
  text-decoration:underline;
}

.btn {
  border:none;
  cursor:pointer;
  font-weight:600;
  border-radius:8px;
  padding:11px 20px;
  font-size:15px;
  background:#333;
  color:#fff;
  transition:.18s;
}
.btn:hover { filter:brightness(1.1); }
.btn:active { transform:translateY(1px); }
.btn:disabled { opacity:.55; cursor:default; }

.btn.red { background:#d72626; }
.btn.green { background:#118c28; }
.btn.dark { background:#444; }
.btn.small { padding:6px 12px; font-size:12px; border-radius:6px; }
.btn.xs { padding:4px 8px; font-size:11px; border-radius:4px; }
.btn.wide { width:100%; }
.soft-lock { position:relative; }
.soft-lock::after {
  content:"(Weekly Lock)";
  font-size:11px;
  margin-left:6px;
  opacity:.8;
}

.logged { margin:0 0 10px; font-size:15px; }

.leader-banner {
  background:#1d1d1d;
  padding:10px 14px;
  border:1px solid #2e2e2e;
  border-radius:10px;
  margin:25px 0 28px;
  text-align:center;
  font-size:14px;
}

.status {
  background:#111;
  border:1px solid #222;
  border-radius:18px;
  padding:26px 28px 28px;
  max-width:620px;
}

.status h2 {
  margin:0 0 12px;
}

.hint, .lock-note {
  font-size:12px;
  color:#bbb;
  margin:8px 0 14px;
}

.coupons {
  list-style:none;
  padding:0;
  margin:20px 0 0;
}
.coupons li {
  padding:10px 14px;
  background:#101010;
  border:1px solid #1f1f1f;
  border-radius:10px;
  margin-bottom:10px;
  font-size:14px;
  display:flex;
  align-items:center;
  gap:6px;
}
.coupons li.faded {
  opacity:.55;
}

.dot {
  width:12px;
  height:12px;
  display:inline-block;
  border-radius:50%;
  position:relative;
  top:1px;
  margin:0 4px;
  box-shadow:0 0 0 1px rgba(255,255,255,.1);
}
.dot.green { background:#1ec845; }
.dot.red { background:#d72626; }

/* Admin */
.admin-block {
  margin-top:50px;
  max-width:700px;
}
.admin-self {
  display:flex;
  align-items:center;
  gap:14px;
  margin-bottom:20px;
  flex-wrap:wrap;
}
.admin-self .last {
  font-size:12px;
  opacity:.75;
}
.admin-users {
  display:grid;
  gap:18px;
}
.user-card {
  background:#111;
  border:1px solid #222;
  padding:16px 18px 14px;
  border-radius:14px;
}
.user-card .head {
  display:flex;
  justify-content:space-between;
  font-size:14px;
  margin-bottom:4px;
}
.user-card .line {
  font-size:12px;
  opacity:.8;
  margin-bottom:10px;
}
.user-card .actions {
  margin-bottom:8px;
}
.admin-coupons {
  list-style:none;
  margin:0;
  padding:0;
  font-size:12px;
}
.admin-coupons li {
  display:flex;
  align-items:center;
  gap:8px;
  padding:4px 0;
}
.admin-coupons .btn.xs { margin-left:8px; }

/* Scanner Modal */
.qr-overlay {
  position:fixed;
  inset:0;
  background:rgba(0,0,0,.78);
  display:flex;
  align-items:flex-start;
  justify-content:center;
  padding:30px 14px;
  z-index:2000;
}
.qr-modal {
  width:100%;
  max-width:480px;
  background:#101010;
  border:1px solid #242424;
  border-radius:22px;
  padding:26px 24px 32px;
  position:relative;
  text-align:center;
  box-shadow:0 8px 40px rgba(0,0,0,.55);
}
.qr-modal h2 {
  margin:0 0 6px;
  font-size:1.9rem;
}
.qr-sub {
  margin:0 0 18px;
  font-size:.86rem;
  color:#bbb;
  line-height:1.3;
}
.qr-frame {
  position:relative;
  width:100%;
  aspect-ratio:1/1;
  background:#000;
  border-radius:20px;
  overflow:hidden;
  margin:0 auto 16px;
  border:2px solid #1d1d1d;
}
.qr-video {
  width:100%;
  height:100%;
  object-fit:cover;
  display:block;
}
.qr-status {
  position:absolute;
  inset:0;
  background:rgba(0,0,0,.82);
  display:flex;
  align-items:center;
  justify-content:center;
  font-size:.95rem;
  padding:10px;
  text-align:center;
  color:#ccc;
}
.qr-border {
  position:absolute;
  inset:0;
  border:5px solid #d72626;
  border-radius:18px;
  pointer-events:none;
  animation:pulse 2.2s infinite;
  box-shadow:0 0 0 4px rgba(215,38,38,.28),0 0 30px rgba(215,38,38,.7);
}
@keyframes pulse {
  0%,100% { box-shadow:0 0 0 4px rgba(215,38,38,.28),0 0 24px rgba(215,38,38,.6); }
  50% { box-shadow:0 0 0 6px rgba(215,38,38,.4),0 0 36px rgba(215,38,38,.95); }
}
.scan-footer {
  font-size:.75rem;
  margin-top:10px;
  color:#888;
  min-height:1em;
}
.scan-footer.success { color:#34d058; }
.scan-footer.error { color:#ff6464; }
.scan-footer.locked { color:#f0ad4e; }

/* Responsive tweaks */
@media (max-width:560px) {
  body::before {
    background:
      radial-gradient(circle at center, rgba(0,0,0,.45), #000 70%),
      url("https://blapoker.com/wp-content/uploads/logo_rev.png") center 90px / 420px auto no-repeat;
    opacity:.32;
  }
  .app-shell { padding:32px 16px 70px; }
  .title { font-size:2.2rem; }
  .status { padding:22px 20px 24px; }
  .qr-modal { padding:22px 20px 30px; }
  .qr-sub { font-size:.78rem; }
}
